#overlay {
    display: none;
    position: absolute;
    left: 20%;
    top: 50px;
    width: 60%;
    text-align:center;
    z-index: 1000;
    background:white;
    border-width: 3px;
    border-style: solid;
    border-color: #003366;
    border-radius: 25px;
 }

 #overlay-background {
    display: none;
    position: absolute;
    left: 0px;
    top: 0px;
    width:100%;
    height:100%;
    text-align:center;
    z-index: 999;
    background: black;
    opacity: 0.55;
    border-radius: 20px;
 }

#gen-text-1, #gen-text-2 {
    font-family: 'Roboto', sans-serif;
    text-align: center;
}

#gen-text-1 {
    font-size: 26px;
}


